<template>
    <Lay :route="route" :arr="arr" :url="url"/>
</template>
<script>
  import Lay from '@/components/common/template';
  import {bit} from '@/config/record'

  export default {
    props: ["route"],
    data() {
      return {
        url: '/data/query/bitdata',
        arr: bit
      }
    },
    components: {
      Lay
    },
    mounted: function () {},
    methods:  {}
  }

</script>




